    <h4 class="fw-bold">Noc deportacji</h4>

    <p class="fw-bold">Zima na przełomie 1939 i 1940 roku okazała się wyjątkowo ostra. Zaspy i mrozy sparaliżowały życie
      prawie w całej
      Europie. Dla walczących wojsk była to pauza. Jednak w okupowanej Polsce zima nie przyniosła wytchnienia. Gestapo i
      NKWD czyniły swoją zbrodniczą powinność. Zwłaszcza Sowieci szykowali się do szeroko zakrojonej akcji na polskich
      Kresach Wschodnich.
    </p>
    <p> Po „wyzwoleniu Zachodniej Białorusi i Zachodniej Ukrainy”, jak propaganda sowiecka nazywała agresję Armii
      Czerwonej
      na Kresy Rzeczypospolitej, przystąpiono błyskawicznie do politycznej unifikacji tych ziem z sowieckim imperium.
      Tempo było rzeczywiście imponujące: 22 października 1939 roku odbyły się „wybory” do zgromadzeń ludowych
      Zachodniej
      Białorusi i Zachodniej Ukrainy. Wyłonieni w czasie tej wyborczej farsy „przedstawiciele ludu” natychmiast
      poprosili
      o włączenie „Zachodniej Białorusi i Zachodniej Ukrainy do wszechzwiązkowej rodziny sowieckich republik”. W
      konsekwencji Rada Najwyższa ZSRS podczas posiedzenia 1 i 2 listopada 1939 roku wyraziła zgodę na włączenie tych
      ziem
      w granice sowieckiego państwa. Wówczas to Wiaczesław Mołotow wypowiedział słynne zdanie, że wystarczyło krótkie
      uderzenie armii niemieckiej i sowieckiej na Polskę, aby nic nie zostało „z tego pokracznego tworu traktatu
      wersalskiego”.</p>

    <h4 class="fw-bold">Wielki terror</h4>

    <p>
      W ślad za decyzją o przyłączeniu Kresów do Związku Sowieckiego przyszła kolejna dyrektywa, która jeszcze bardziej
      miała zmienić w koszmar życie ich mieszkańców. 29 listopada Rada Najwyższa ZSRS nadała obywatelstwo sowieckie
      wszystkim osobom, które 1 i 2 listopada zamieszkiwały na anektowanych terenach. 30 grudnia 1939 roku ukazał się
      dekret o wydaniu im paszportów, czyli sowieckich dowodów osobistych. Ta przymusowa „paszportyzacja” posłużyła NKWD
      za doskonały sposób do zebrania lub uzupełnienia danych o ludności Kresów, zwłaszcza polskiej, która wówczas
      stanowiła dla sowieckiej służby bezpieczeństwa największy problem. Ci, którym przypisano obywatelstwo sowieckie z
      miejsca podlegali poborowi do Armii Czerwonej. W ten sposób do jej batalionów roboczych od razu wcielono 150 tys.
      Polaków! Ponadto NKWD w grudniu 1939 roku nasiliło falę aresztowań. Wówczas, głównie w kresowych miastach,
      uwięziono
      wielu byłych pracowników polskiej administracji, sądownictwa, działaczy społecznych i gospodarczych oraz oficerów
      rezerwy. Ludzie ci byli na mocy wyroków jawnych lub tajnych trybunałów wywożeni do obozów pracy. I to był dopiero
      początek – rozruch wielkiej machiny terroru, którą NKWD stworzyło dla ujarzmienia Kresów.
    </p>
    <p>
      Enkawudziści doskonale zdawali sobie sprawę, że żadna propaganda ani indoktrynacja nie zmieni Polaków w
      „sowieckich
      ludzi”. Zresztą mieli tego wyraźne dowody. Między innymi 21 stycznia 1940 roku w Czortkowie (wówczas już w
      Ukraińskiej Socjalistycznej Republice Sowieckiej) grupa polskiej młodzieży licealnej, około 120-150 osób, wywołała
      powstanie. Częściowo uzbrojona młodzież opanowała dworzec kolejowy, aby w perspektywie przejąć kontrolę nad
      szlakiem
      komunikacyjnym do Rumunii, skąd można było przedostać się do armii polskiej we Francji. Po pierwszej panice
      żołnierzy garnizonu sowieckiego bunt szybko stłumiono. Ośmiu buntowników stracono w Odessie, a resztę uwięziono,
      ale
      to pokazywało, że Mołotow przedwcześnie ogłosił definitywny koniec Polski. NKWD sięgnęła wówczas do sprawdzonych
      metod represjonowania nie tylko pojedynczych ludzi, ale całych środowisk i grup społecznych. Zaczęto gromadzić na
      masową skalę składy kolejowe i wyznaczać trasy deportacji w najdalsze rejony przepastnego imperium sowieckiego.
    </p>

    <h4 class="fw-bold">Mroźny koszmar</h4>

    <p>
      Wraz z kolejną falą mrozów i śnieżyc NKWD w nocy z 9 na 10 lutego 1940 roku rozpoczęła swą akcję deportacyjną. Ta
      straszna noc w tysiącach polskich miejscowości, osiedli i przysiółków miała ten sam, szczegółowo zaplanowany przez
      enkawudzistów przebieg. Według list proskrypcyjnych otaczano dom wyznaczonej do deportacji rodziny. Brutalnie
      wyrwanym ze snu ludziom pozwalano zabrać rzeczy osobiste, naczynia kuchenne i żywność na kilka tygodni. Następnie
      przesiedleńców wieziono saniami lub pędzono pieszo do najbliższej stacji kolejowej, gdzie na bocznicy czekały już
      na
      nich bydlęce wagony.
    </p>
    <p>
      Tak tę chwilę zapamiętała mieszkanka Lipowca pod Drohobyczem, wówczas nastoletnia Adela
      Żurawska: „Żołnierze sowieccy, którzy otoczyli nasz dom, dali nam chwilę na spakowanie najważniejszych rzeczy i
      załadowali wszystkich do sań. Zostałyśmy dowiezione do stacji kolejowej w Zborowie. Tam już stał pod parą bardzo
      długi pociąg złożony z bydlęcych wagonów. W naszym ściśnięto około pięćdziesięciu osób. Ruszyliśmy po czterech czy
      pięciu dniach na wschód. […] Podniósł się wtedy jeden płacz – ludzie rozpaczali i się modlili”. Ta lutowa
      deportacja
      dotknęła przede wszystkim osadników wojskowych, leśników i innych urzędników państwowych oraz kolejarzy,
      oczywiście
      wraz z rodzinami. Jeśli ojcom rodzin udowodniono, że byli oficerami, przedsiębiorcami czy ziemianami, po wyroku
      sądów doraźnych kierowano ich do łagrów. Resztę deportowanych w lutym 1940 roku – czyli głównie kobiety z dziećmi
      oraz starców – wywożono na Ural i Syberię, gdzie w dramatycznie trudnych warunkach musieli „rozpoczynać życie na
      nowo”. Wielu z nich nie dane było przeżywanie tego koszmaru, umarli z wychłodzenia i głodu w czasie transportu.
      Akcja z 9 na 10 lutego 1940 była dopiero pierwszą z całej serii deportacji, którą NKWD zaplanowało, by raz na
      zawsze
      oczyścić zdobyczne ziemie z „polskiego żywiołu”. W kwietniu ofiarami masowych wywózek padły rodziny jeńców,
      skazanych i aresztowanych. W ten sposób w Kazachstanie znaleźli się między innymi najbliżsi polskich oficerów
      mordowanych w tym samym czasie w Katyniu. W czerwcu deportacją objęto uciekinierów z centralnej i zachodniej
      Polski.
    </p>
    <p>
      Wbrew temu, co dzisiaj głosi prezydent Rosji, Władimir Putin, wśród wywożonych wówczas obywateli polskich znalazło
      się tysiące Żydów, którzy uciekli przed represjami sojusznika Stalina – Hitlera. Wielu z nich nie przeżyło
      transportu w bydlęcych wagonach lub zmarło na miejscu zesłania – w Archangielsku i Krasnojarsku.
      Wreszcie ostatnia fala deportacji, przeprowadzona przed samym wybuchem wojny niemiecko-sowieckiej w maju i czerwcu
      1941 roku, dotknęła uciekinierów z obszaru Wileńszczyzny. Wywożono ich do Kazachstanu i na Syberię dosłownie do
      ostatnich dni pokoju między III Rzeszą a Związkiem Sowieckim. Wiele z pociągów z deportowanymi ludźmi Wehrmacht
      przejął po wkroczeniu na Kresy 22 czerwca 1941 roku. Bardzo trudno jest oszacować, ilu obywateli polskich
      wywieziono
      wówczas podczas tych czterech faz deportacji. Najczęściej wymienia się liczbę 400-500 tysięcy. Pewne jest jedno –
      wszystkich czekała gehenna w „sowieckim raju”. Ci, którzy mieli szczęście przetrwać, po „amnestii” w lipcu 1941
      roku
      trafili do armii polskiej generała Władysława Andersa i czekał ich exodus na Zachód. A ci, którzy „nie zdążyli do
      Andersa” wrócili do Polski w szeregach wojska generała Zygmunta Berlinga. Taki los spotkał cytowaną wyżej Adelę
      Żurawską.
    </p>

